.out_shadow {
  box-shadow:  7px 7px 0 #000000,
  -7px -7px 0 #ffffff;
}
.in_shadow {
  box-shadow: inset 7px 7px 0 #000000,
  inset -7px -7px 0 #ffffff;
}

.bg {
  background-color: #b7b3b7;
}
.inset_neo {
  /*background-color: #e0eaf9;*/
  -webkit-box-shadow: inset 2px 2px 4px rgba(56,53,85,0.3), inset -2px -2px 4px rgba(255,255,255,0.5), inset 0 0 100px rgba(56,53,85,0.02);
  box-shadow: inset 2px 2px 4px rgba(56,53,85,0.3), inset -2px -2px 4px rgba(255,255,255,0.5), inset 0 0 100px rgba(56,53,85,0.02);
