.gd_bg {
  background: linear-gradient(150deg,  rgba(232,231,238,1) 50%, rgb(152, 49, 181) 50%);
}
.gd_bg-v {
  background: linear-gradient(0deg,  rgba(232,231,238,1) 0%, rgb(152, 49, 181) 50%);
}
.gd_bg-v-r {
  background: linear-gradient(200deg,  rgba(232,231,238,1) 50%, #DAFA18 50%);
}
.hide-scroll-bar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.hide-scroll-bar::-webkit-scrollbar {
  display: none;
}

.bg_tr{
  /*position: relative;*/
  /*width: 99vw;*/
  /*height: 40vh;*/
